@font-face {
  font-family: digital;
  src: url("ds-digit.f5c1e273.ttf");
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  width: 500px;
  height: 200px;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  display: grid;
}

.field {
  min-width: 60px;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  position: relative;
}

.field:not(:last-child):after {
  content: ":";
  font-size: 40px;
  position: absolute;
  top: 48px;
  right: -15px;
}

.value {
  font-family: digital;
  font-size: 48px;
}

.label {
  color: gray;
  font-size: 24px;
}

